<template>
	<div :id="'message-'+message.message_id">
		<div v-if="canShowDateTag()" style="text-align:center; margin-bottom: 10px;">
		 <span style="background-color: #efefef; border-radius: 5px; padding:8px;">{{canShowDateTag()}}</span>	
		</div>
	<div :class="[message.user && loggedUser.uid==message.user.user_name ? 'my-message':'other-message']">
		<div class="message-container" :class="[message.is_private==true ? 'private':'']" v-if="message.message_type!=6">
		
			<div  v-if="message.message_type!=6 && message.trashed!=true">
				<span class="message-label" v-if="message.is_private==true">Private</span>
				<span class="message-label" v-if="message.is_reminder==true">Reminder</span>
				<b v-if="message.user.user_name!=loggedUser.uid" class="message-user">{{message.user.full_name}}</b>
				
				<!-- Reply object -->
				<a v-if="message.reply" :href="'#message-'+message.reply.message_id" :id="'reply-'+message.reply.message_id">
					<div v-if="message.reply" class="reply-container" @click="scrollToReply(message.reply)">
						
						<b v-if="message.reply.user.user_name!=loggedUser.uid" class="reply-message-user">
							{{ message.reply.user.full_name }}
						</b>
						<div>
							<AaochtMessageAttachment  :attachments="message.reply.attachments" />
							<pre class="message-text">{{message.reply.message}}</pre>
						</div>
						
					</div>
				</a>
				<!-- End of reply object -->
				<div>
					<AaochtMessageAttachment  :attachments="message.attachments" />
					<pre class="message-text">{{message.message}}</pre>
				</div>
			</div>

			<div class="trashed" v-if="message.trashed==true">
				<span class="message-text">This message is deleted.</span>
			</div>

			<div class="created-at" v-if="message.message_type!=6 && message.trashed!=true">
				<small class="message-time" >{{messageLocalTime()}}</small>
				<small v-show="message.user.user_name==loggedUser.uid && message.is_reminder!=1"><i :class="[message.seen ? 'icon-double-tick-icon seen-msg': message.delivered ?  'icon-double-tick-icon gray-txt' : 'icon-single-tick-icon gray-txt']"></i></small>
			</div>
				
		</div>
	</div>
	</div>
</template>

<script>

import AaochtMessageAttachment from './AaochtMessageAttachment.vue'

/**
 * @typedef RichObject
 * @type {object}
 * @property {string} id - The id of the riche object.
 * @property {string} type - The type of the file object.
 */

export default {
	name: 'AaochtMessage',
	components: {
		AaochtMessageAttachment
	},
	props: {
		/**
		 * The AaochtMessage to render.
		 */
		previousMessageTimestamp:{
			type: String,
			required: false,
		},
		message: {
			type: Object,
			required: true,
		},
	},
	data() {
		return {
			dateFromNow: '',
			loggedUser: OC.getCurrentUser(),
			dateInterval: 0,
		}
	},
	computed: {
		
	},
	created() {

	},
	destroyed() {

	},
	methods: {

		getDateFromUTCString(dateString){
			const messageDate = new Date(dateString);
			const options = { year: 'numeric', month: 'short', day: 'numeric' };
			const formatedDate = messageDate.toLocaleDateString(undefined, options);
			return formatedDate;
		},

		canShowDateTag(){

			if(this.previousMessageTimestamp==null){
				return this.getDateFromUTCString(this.message.created_at);
			}

			const previousDate = this.getDateFromUTCString(this.previousMessageTimestamp);
			const messageDate = this.getDateFromUTCString(this.message.created_at);

			if(messageDate==previousDate){
				return false;
			}else{
				return messageDate;
			}
		},	

		messageLocalTime() {
			const messageDate = new Date(this.message.created_at);
			const options = { year: 'numeric', month: 'short', day: 'numeric' };
			const formatedDate = messageDate.toLocaleDateString(undefined, options);
			const formatedTime = messageDate.toLocaleTimeString('en-US', {
				hour:   '2-digit',
				minute: '2-digit',
			});
          return formatedDate+' '+formatedTime;
		},
		scrollToReply(replyMessage) {
			//console.log(replyMessage);
			if(document.getElementById("message-"+replyMessage.message_id) == null) {
				this.$parent.scrollToReply(replyMessage);
			}
		}
	},
}
</script>

