`, "",{"version":3,"sources":["webpack://./src/views/AaochatTab.vue"],"names":[],"mappings":";AAmbA;CACA,YAAA;CACA,MAAA;AACA","sourcesContent":["<!--\n  - @copyright Copyright (c) 2023 Aao Business Chat <info@aaochat.com>\n  -\n  - @author Aao Business Chat <info@aaochat.com>\n  -\n  - @license AGPL-3.0-or-later\n  -\n  - This program is free software: you can redistribute it and/or modify\n  - it under the terms of the GNU Affero General Public License as\n  - published by the Free Software Foundation, either version 3 of the\n  - License, or (at your option) any later version.\n  -\n  - This program is distributed in the hope that it will be useful,\n  - but WITHOUT ANY WARRANTY; without even the implied warranty of\n  - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n  - GNU Affero General Public License for more details.\n  -\n  - You should have received a copy of the GNU Affero General Public License\n  - along with this program. If not, see <http://www.gnu.org/licenses/>.\n  -\n  -->\n<template>\n\t<div :class=\"{ 'icon-loading': loading }\" style=\"display:flex; flex:1;\">\n\t\t<!-- error message -->\n\t\t<template  v-if=\"error\" :title=\"error\">\n\t\t\t<div class=\"aaochat-tab-view\">\n\t\t\t\t<div class=\"aaochat-start-conversion\">\n\t\t\t\t\t<span><a href=\"#\" class=\"\" @click=\"showSendMessageForm\">\n\t\t\t\t\tStart Conversation\n\t\t\t\t\t</a></span>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</template>\n\t\t<template v-else>\n\t\t\t<!-- aaochtMessages content -->\n\t\t\t<div class=\"aaochat-tab-view\" v-if=\"channelId\">\n\n\t\t\t\t<div class=\"aaochat-message-list\" v-on:scroll.native=\"handleScroll\" v-on:scroll=\"handleScroll\">\n\t\t\t\t\t<AaochtMessage v-for=\"(message, index) in aaochtMessages\"\n\t\t\t\t\t\t:key=\"message.message_id\"\n\t\t\t\t\t\t:previousMessageTimestamp=\"(index==aaochtMessages.length-1? null: aaochtMessages[index+1].created_at)\"\n\t\t\t\t\t\t:message=\"message\" />\n\t\t\t\t</div>\n\n\t\t\t\t<div class=\"aaochat-message-form-wrapper\">\n\t\t\t\t\t<div class=\"aaochat-message-form\">\n\t\t\t\t\t\t<form v-on:submit.prevent=\"onSubmit\" class=\"chat-message\" onsubmit=\"return false;\" action=\"#\">\n\t\t\t\t\t\t\t<div class=\"row aaochat-send-message-row\">\n\t\t\t\t\t\t\t\t<div class=\"col-md-12\">\n\t\t\t\t\t\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t\t\t\t\t\t<div style=\"\" emoji-form=\"\" emoji-message=\"emojiMessage\" id=\"newMessageContainer\">\n\t\t\t\t\t\t\t\t\t\t\t<textarea id=\"txtMessage\" v-model=\"txtMessage\" @keyup.enter.exact=\"sendAaochatMessage\" class=\"form-control input-lg ng-pristine ng-untouched ng-valid \" placeholder=\"Type your message here...\" style=\"\"></textarea>\n\t\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t\t\t<div class=\"input-group-btn\">\n\t\t\t\t\t\t\t\t\t\t\t<ActionButton type=\"button \" class=\"aaochat-send-message-btn\" @click=\"sendAaochatMessage\">\n\t\t\t\t\t\t\t\t\t\t\t\t<img src=\"../../img/send.png\" alt=\"\">\n\t\t\t\t\t\t\t\t\t\t\t</ActionButton>\n\t\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t</form>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\n\t\t\t<template v-if=\"channelId==null && !loading\" :title=\"t('aaochat', '')\">\n\t\t\t\t<div class=\"aaochat-tab-view\">\n\t\t\t\t\t<div class=\"aaochat-start-conversion\">\n\t\t\t\t\t\t<span><a href=\"#\" class=\"\" @click=\"showSendMessageForm\">\n\t\t\t\t\t\tStart Conversation\n\t\t\t\t\t\t</a></span>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</template>\n\t\t</template>\n\t</div>\n</template>\n\n<script>\nimport { generateOcsUrl } from '@nextcloud/router'\nimport { translate } from '@nextcloud/l10n'\nimport axios from '@nextcloud/axios'\nimport NcEmptyContent from '@nextcloud/vue/dist/Components/NcEmptyContent.js'\n\nimport AaochtMessage from './AaochtMessage.vue'\nimport socketService from '../socketio_service.js'\n\nimport logger from '../logger.js'\n\nexport default {\n\tname: 'AaochatTab',\n\tcomponents: {\n\t\tAaochtMessage,\n\t\tNcEmptyContent,\n\t},\n\tcreated(){\n\t\tconst socket = socketService.setupSocketConnection(localStorage.getItem(\"nextcloud-AaoChatServerURL\"));\n\t\tsocket.on('connect', () => {\n\t\t\tconsole.log('connected inside');\n\t\t});\n\t\tsocket.on('message', (data) => {\n\t\t\t\n\t\t\tconsole.log('new message received', data);\n\t\t\tif(this.channelId!=data.channel_id) {\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tconst existingMessage = this.aaochtMessages.find(function(item){\n\t\t\t\treturn item.message_id == data.message_id;\n\t\t\t});\n\t\t\tif(!existingMessage){\n\t\t\t\tdata.delivered = data.delivered||false;\n\t\t\t\tdata.seen = data.seen||false;\n\t\t\t\tdata.trashed = data.trashed||false;\n\t\t\t\tthis.aaochtMessages.unshift(data);\n\t\t\t}\n\n\t\t\tsocket.emit('user:message-seen',{\n\t\t\t\tmessage_id: data.message_id,\n            \tuser_id: data.user_id,\n            \tchannel_id: data.channel_id,\n                is_splash: data.is_splash          \n\t\t\t});\n\t\t\t\n\t\t});\n\n\n\t\tsocket.on('message-received', (data)=>{\n\t\t\tconsole.log('message-received',data);\n\t\t\tconst existingMessageIndex = this.aaochtMessages.findIndex(function(item){\n\t\t\t\treturn item.message_id == data.message_id;\n\t\t\t});\n\n\t\t\tif(existingMessageIndex==-1){\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tconst newMessage= this.aaochtMessages[existingMessageIndex];\n\t\t\tnewMessage.delivered = true;\n\n\t\t\tthis.aaochtMessages.splice(existingMessageIndex,1,newMessage);\n\n\t\t});\n\n\t\tsocket.on('message-seen', (data)=>{\n\t\t\tconsole.log('message-seen',data);\n\t\t\tconst existingMessageIndex = this.aaochtMessages.findIndex(function(item){\n\t\t\t\treturn item.message_id == data.message_id;\n\t\t\t});\n\n\t\t\tif(existingMessageIndex==-1){\n\t\t\t\tconsole.log('message-seen not found.');\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tconst newMessage= this.aaochtMessages[existingMessageIndex];\n\t\t\tnewMessage.seen = true;\n\t\t\t\n\t\t\t//Vue.set(this.aaochtMessages,existingMessageIndex,newMessage)\n\t\t\tthis.aaochtMessages.splice(existingMessageIndex,1,newMessage);\n\n\t\t})\n\n\t\tsocket.on('message-deleted', (data)=>{\n\n\t\t\tconst existingMessageIndex = this.aaochtMessages.findIndex(function(item){\n\t\t\t\treturn item.message_id == data.message_id;\n\t\t\t});\n\n\t\t\tif(existingMessageIndex==-1){\n\t\t\t\treturn;\n\t\t\t}\n\t\t\t\n\t\t\tconst newMessage= this.aaochtMessages[existingMessageIndex];\n\t\t\tnewMessage.trashed = true;\n\n\t\t\tthis.aaochtMessages.splice(existingMessageIndex,1,newMessage);\n\n\t\t})\n\t},\n\tdata() {\n\t\treturn {\n\t\t\terror: '',\n\t\t\tloading: true,\n\t\t\tloadingMessages: false,\n\t\t\tfileInfo: null,\n\t\t\tchannelId: null,\n\t\t\taaochtChannel: [],\n\t\t\taaochtMessages: [],\n\t\t\taaochtRootUser: {},\n\t\t\ttxtMessage:'',\n\t\t\tlastScrollTop:0\n\t\t}\n\t},\n\tmethods: {\n\t\tonSubmit () {\n\t\t\t// DO Something\n\t\t\treturn false;\n\t\t},\n\t\t/**\n\t\t * \n\t\t */\n\t\thandleScroll(data) {\n\t\t\t//console.log('handleScroll');\n\t\t\t//console.log(data.srcElement.offsetHeight, data.srcElement.scrollTop, data.srcElement.scrollHeight);\n\n\t\t\tvar st = data.srcElement.scrollTop;\n\t\t\tif (st > this.lastScrollTop){\n\t\t\t\t// downscroll code\n\t\t\t\tconsole.log('scroll down');\n\t\t\t} else {\n\t\t\t\t// upscroll code\n\t\t\t\tconsole.log('scroll up');\n\t\t\t\tif(Math.abs(data.srcElement.scrollTop)+data.srcElement.offsetHeight + 10 >= data.srcElement.scrollHeight){\n\t\t\t\t\tthis.getMessageHistory(false);\n\t\t\t\t}\n\t\t\t}\n            this.lastScrollTop = st;\n\n\t\t},\n\t\t/**\n\t\t * Update current fileInfo and fetch new activities\n\t\t *\n\t\t * @param {object} fileInfo the current file FileInfo\n\t\t */\n\t\tasync update(fileInfo) {\n\t\t\tthis.fileInfo = fileInfo\n\t\t\tthis.resetState()\n\t\t\tawait this.getAaochatConversions()\n\t\t},\n\t\t/**\n\t\t * Get the existing aaocht messages\n\t\t */\n\t\tasync getAaochatConversions() {\n\t\t\t//console.log('Ashok test');\n\t\t\t//console.log(this.fileInfo);\n\t\t\ttry {\n\t\t\t\t\n\t\t\t\tconst u = OC.getCurrentUser(); \n\n\t\t\t\tthis.loading = true\n\t\t\t\tlet api_url = OC.generateUrl('/apps/aaochat/getgroup'+'?fileid='+this.fileInfo.id)\n\t\t\t\tconst aaochtChannel = await axios.get(api_url)\t\t\t\t\n\t\t\t\tconsole.log(aaochtChannel);\n\n\n\t\t\t\tvar responseData = JSON.parse(aaochtChannel.data);\n\t\t\t\tconsole.log(responseData);\n\t\t\t\tif(responseData.status == 'error') {\n\t\t\t\t\t//this.startConversionDisplay($el, responseData);\n\t\t\t\t\tthis.loading = false\n\t\t\t\t} else if(responseData.status == 'success') {\n\t\t\t\t\tconst channelData = responseData.data;\n\t\t\t\t\tconst channelUsers = channelData.users;\n\t\t\t\t\tthis.aaochtRootUser = channelUsers.find(function(singleUser){\n\t\t\t\t\t\treturn singleUser.user_name == u.uid;\n\t\t\t\t\t});\n\t\t\t\t\tthis.aaochtChannel = channelData;\n\t\t\t\t\tthis.channelId = channelData._id;\t\t\t\t\t\n\t\t\t\t\tthis.loading = false\n\t\t\t\t\tthis.getMessageHistory();\n\t\t\t\t}\n\t\t\t} catch (error) {\n\t\t\t\t// Status 304 is not an error.\n\t\t\t\tif (error.response !== undefined && error.response.status === 304) {\n\t\t\t\t\tthis.loading = false\n\t\t\t\t\treturn\n\t\t\t\t}\n\t\t\t\tthis.error = translate('aaochat', 'Unable to load the aaochtChannel')\n\t\t\t\tthis.loading = false\n\t\t\t\tconsole.error('Error loading the aaochtChannel', error)\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t * Get the existing aaocht messages\n\t\t */\n\t\tasync getMessageHistory(isPreviousLoad = false) {\n\t\t\ttry {\n\n\t\t\t\tif(this.loadingMessages==true){\n\t\t\t\t\treturn;\n\t\t\t\t}\n\n\t\t\t\tif(isPreviousLoad==false){\n\t\t\t\t\tthis.loading = true;\n\t\t\t\t}\n\t\t\t\t\n\t\t\t\tthis.loadingMessages =  true;\n\t\t\t\tlet authKey = '';\n\t\t\t\tlet aaoChatServerURL = '';\n\t\t\t\tif (typeof(Storage) !== \"undefined\") {\n\t\t\t\t\tauthKey = localStorage.getItem(\"ngStorage-AuthKey\").replace(/\"/g,'');\n\t\t\t\t\taaoChatServerURL = localStorage.getItem(\"nextcloud-AaoChatServerURL\");\n\t\t\t\t}\n\t\t\t\tlet postdata = {channel_id:this.channelId};\n\n\t\t\t\tif(this.aaochtMessages.length > 0){\n\t\t\t\t\tpostdata.message_id = this.aaochtMessages[this.aaochtMessages.length-1].message_id;\n\t\t\t\t}\n\n          \t\tlet headers = {\"Authorization\": authKey,'content-type':'application/json'}\n          \t\tlet api_url = aaoChatServerURL+'/api/channel/messages?fetch_notification_messages=false';\n\n\t\t\t\tconst aaochtMessages = await axios.post(api_url,postdata,{headers});\n\t\t\t\tthis.aaochtMessages.push(...aaochtMessages.data.messages);\n\t\t\t\t\n\t\t\t\tthis.loadingMessages = false;\n\t\t\t\tthis.loading = false;\n\t\t\t\t// this.processAaochtMessages(aaochtMessages)\n\t\t\t} catch (error) {\n\t\t\t\t// Status 304 is not an error.\n\t\t\t\tif (error.response !== undefined && error.response.status === 304) {\n\t\t\t\t\tthis.loadingMessages = false;\n\t\t\t\t\tthis.loading = false;\n\t\t\t\t\treturn\n\t\t\t\t}\n\t\t\t\tthis.error = translate('aaochat', 'Unable to load the messages')\n\t\t\t\tthis.loading = false;\n\t\t\t\tthis.loadingMessages = false;\n\t\t\t\tconsole.error('Error loading the messages', error)\n\t\t\t} \n\t\t},\n\t\t/**\n\t\t * Reset the current view to its default state\n\t\t */\n\t\tresetState() {\n\t\t\tthis.loading = true\n\t\t\tthis.error = ''\n\t\t\tthis.loadingMessages = false;\n\t\t\tthis.aaochtMessages=[];\n\t\t\tthis.channelId=null;\n\t\t\tthis.lastScrollTop=0;\n\t\t},\n\t\t/**\n\t\t * Process the current messages data\n\t\t * and init messages[]\n\t\t *\n\t\t * @param {object} messages the activity ocs api request data\n\t\t * @param {object} messages.data the request data\n\t\t */\n\t\tprocessAaochtMessages({ data }) {\n\t\t\tif (data.ocs && data.ocs.data && data.ocs.data.length > 0) {\n\t\t\t\t// create Activity objects and sort by newest\n\t\t\t\t/*this.messages = data.ocs.data\n\t\t\t\t\t.map(activity => new ActivityModel(activity))\n\t\t\t\t\t.sort((a, b) => b.timestamp - a.timestamp)*/\n\t\t\t\tthis.messages = {}\n\t\t\t\tlogger.debug(`Processed ${this.messages.length} aaochat messages`, { messages: this.messages, fileInfo: this.fileInfo })\n\t\t\t}\n\t\t},\n\t\tt: translate,\n\t\t/**\n\t\t *\n\t\t */\n\t\tasync showSendMessageForm() {\n\t\t\tconst u = OC.getCurrentUser(); \n\n\t\t\tthis.loading = true\n\t\t\tlet api_url = OC.generateUrl('/apps/aaochat/syncshareinfo?fileid='+this.fileInfo.id)\n\t\t\tconst aaochtChannel = await axios.get(api_url)\t\t\t\t\n\t\t\tconsole.log(aaochtChannel);\n\n\t\t\tvar responseData = aaochtChannel.data;\n\t\t\tconsole.log(responseData);\n\t\t\tif(responseData.status == 'error') {\n\t\t\t\tthis.loading = false\n\t\t\t} else if(responseData.status == 'success') {\n\t\t\t\tconst channelData = responseData.data;\n\t\t\t\tconst channelUsers = channelData.users;\n\t\t\t\tthis.aaochtRootUser = channelUsers.find(function(singleUser){\n\t\t\t\t\treturn singleUser.user_name == u.uid;\n\t\t\t\t});\n\t\t\t\tthis.aaochtChannel = channelData;\n\t\t\t\tthis.channelId = channelData._id;\n\t\t\t\tthis.loading = false\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t *\n\t\t */\n\t\tasync sendAaochatMessage() {\n\t\t\t//this.loading = true\n\t\t\tlet authKey = '';\n\t\t\tlet aaoChatServerURL = '';\n\t\t\t//console.log(this.txtMessage);\n\t\t\tif (typeof(Storage) !== \"undefined\") {\n\t\t\t\tauthKey = localStorage.getItem(\"ngStorage-AuthKey\").replace(/\"/g,'');\n\t\t\t\taaoChatServerURL = localStorage.getItem(\"nextcloud-AaoChatServerURL\");\n\t\t\t}\n\n\t\t\tif(this.txtMessage!=null && this.txtMessage.trim().length > 0) {\n\t\t\t\tconst text = this.txtMessage.trim();\n\t\t\t\tthis.txtMessage = '';\n\t\t\t\tlet postdata = {channels:this.channelId, message:text};\n\t\t\t\tlet headers = {\"Authorization\": authKey,'content-type':'application/json'}\n\t\t\t\tlet api_url = aaoChatServerURL+'/api/channel/message/upload-file-and-send-message';\n\n\t\t\t\tconst response = await axios.post(api_url,postdata,{headers});\n\t\t\t}\n\t\t\