meString('en-US', {\n\t\t\t\thour:   '2-digit',\n\t\t\t\tminute: '2-digit',\n\t\t\t});\n          return formatedDate+' '+formatedTime;\n\t\t},\n\t\tscrollToReply(replyMessage) {\n\t\t\t//console.log(replyMessage);\n\t\t\tif(document.getElementById(\"message-\"+replyMessage.message_id) == null) {\n\t\t\t\tthis.$parent.scrollToReply(replyMessage);\n\t\t\t}\n\t\t}\n\t},\n}\n</script>\n\n<style lang=\"css\" scoped>\n\t.message-container {\n\t\tposition:relative;\n\t\tpadding:10px 15px 20px;\n\t\tborder-radius:5px;\n\t\tmax-width: 70%;\n\t\tmin-width: 150px;\n\t}\n\n\t.message-text{\n\t\twhite-space: pre-wrap;       /* css-3 */\n\t\twhite-space: -moz-pre-wrap;  /* Mozilla, since 1999 */\n\t\twhite-space: -pre-wrap;      /* Opera 4-6 */\n\t\twhite-space: -o-pre-wrap;    /* Opera 7 */\n\t\tword-wrap: break-word;   \n\t\toverflow-wrap: anywhere;\n\t}\n\t.created-at {\n\t\tposition: absolute;\n\t\tbottom: 1px;\n\t\tright: 10px;\n\t}\n\t.message-time {\n\t\tcolor: #a8aab1;\n\t\tpadding-left: 6px;\n\t\tfont-size: 11px;\n\t}\n\t\n\t.my-message{\n\t\tdisplay:flex;\n\t\tjustify-content: end;\n\t\tmargin-bottom:10px;\n\t}\n\n\t.my-message .message-user{\n\t\tdisplay:none;\n\t}\n\n\t.other-message {\n\t\tdisplay:flex;\n\t\tjustify-content: start;\n\t\tmargin-bottom:10px;\n\t}\n\n\t.other-message .message-user{\n\t\tcolor:#f95100;\n\t}\n\t.my-message .reply-message-user{\n\t\tcolor:#f95100;\n\t}\n\n\t#tab-aaochat .message-container.private {\n\t\tbackground-color: #fcf8e3 !important;\n    \tborder: 1px solid #ece8cf;\n\t}\n\t#tab-aaochat .alert-info {\n\t\tcolor: #31708f;\n\t\tbackground-color: #d9edf7;\n\t\tborder-color: #bce8f1;\n\t}\n\t#tab-aaochat .message-container .private .message-label {\n\t\tfont-size: 11px;\n\t\tcolor: gray;\n\t\tline-height: 14px;\n\t}\n\t#tab-aaochat .trashed .message-text {\n\t\tfont-size: 14px;\n\t}\n\n\t.my-message .message-container {\n\t\tbackground-color:#d9edf7;\n\t}\n\n\t.other-message .message-container {\n\t\tbackground-color:#dcdcdc;\n\t}\n\t