	}
	#tab-aaochat .file-icon-container {
		padding: 5px;
		background-color: white;
		text-align: center;
		width: 40px;
		height: 40px;
		border-radius: 5px;
		margin-right: 15px;
	}

	#tab-aaochat .file-icon-container img {
		height: 30px;
		vertical-align: middle;
		border: 0;
	}
	
</style>